@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Narrow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Mono&display=swap);
.header {
  width: 100%;
}

.header__inner {
  max-width: 300px;
  margin: 0 auto;
  border-top: 1px solid var(--black);
  margin-top: 30px;
  padding-top:15px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
}

.header__logo {
  font-family: 'Playfair Display', serif;
  font-size: 22px;
  border-color: var(--black);
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  padding: 0 2px; 
}

.nav__list {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
}

.header__nav {
  border-left:1px solid var(--black);
  height: 100%;;
}

.nav__item {
  padding: 5px 5px;
  position: relative;
}

.nav__link {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 300;
  color: var(--black);
}

.nav__item::after {
  content: '';
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: 0;
  left: 0;
  background: transparent;
  transition: .3s;
}
.nav__item:hover::after {
  background: var(--black);
}

@media screen and (min-width: 375px) {
  .header__inner {
    max-width: none;
    margin: 30px;
  }
}

@media screen and (min-width: 1440px) {
  .header__inner {
    max-width: 1250px;
    margin: 30px auto;
  }
}
.canvas__wrapper {
  width: 100%;
  height: 300px;
  max-width: 300px;
}

@media screen and (min-width: 1024px) {
  .canvas__wrapper {
    min-width: 500px;
    height: 500px;
  }
}

.message--right {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
} 

.message__author {
  background-color: #faf2ec;
  display: inline-block;
  padding: 5px 10px;
  border-radius: 8px;
  color: #362d22;
  margin-bottom: 15px;

}

.message__time {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
  opacity: 0.8;
}

.message {
  padding: 15px;
  border-radius: 16px;
  font-family: 'Open Sans', sans-serif;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.section {
  width: 100%;
}

.promo__inner {
  max-width: 300px;
  margin: 30px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.promo__title {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 2rem;
}

.project__inner {
  max-width: 300px;
  margin: 0 auto;
}

.project__title {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 30px;
  border: 1px solid var(--black);
  padding: 10px;
}

.project__message {
  background-color: var(--message-back);
  margin-bottom: 30px;
  max-width: 80%;
}

.project__message--important {
  font-weight: 500;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%!important;
  animation: gradient 15s ease infinite;
  color: var(--white);
}

.team__inner {
  max-width: 300px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: max(80vh, 400px);
}

.team__title {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 2rem; 
  text-align: center;
  margin-bottom: 30px;
  border: 1px solid var(--black);
  padding: 10px;
}

.team_devs {
  display: flex;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  flex-direction: column;
  margin: 0 auto;
}

.devs__links {
  display: flex;
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  grid-gap: 1em;
  gap: 1em;
  margin-top: 1em;
}

.stack__inner {
  max-width: 300px;
  margin: 0 auto;
}

.stack__title {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 2rem; 
  text-align: center;
  margin-bottom: 30px;
  border: 1px solid var(--black);
  padding: 10px;
}

@media screen and (min-width: 375px) {
  .promo__inner,
  .project__inner,
  .team__inner,
  .stack__inner {
    max-width: none;
    margin: 30px;
  }
  
}


@media screen and (min-width: 768px) {
  .promo__inner {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
  .team_devs {
    flex-direction: row;
    margin: 0 0;
  }
}

@media screen and (min-width: 1440px) {
  .promo__inner,
  .project__inner,
  .team__inner,
  .stack__inner {
    max-width: 1250px;
    margin: 30px auto;
  }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.dev__name,
.dev__resp,
.dev__text {
  text-align: center;
}

.dev__img {
  margin: 0 auto;
}

.dev {
  /* border:1px solid var(--black); */
  padding: 15px;
  border-radius: 16px;
  /* color: var(--white); */
  font-family: 'PT Mono', monospace;
}
.socialLink:hover {
  opacity: 0.8;
}

.socialLink {
  width: 60px;
  height: 60px;
  display: block;
}

.socialLink__image {
  width: 100%;
}
.footer {
  background: var(--black);
  color: var(--white);
  text-align: center
}
.cursor {
  z-index: 10000;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  border: 1px solid var(--black);
  pointer-events: none;
  overflow: hidden;
  transform: translate3d(0,0,0);
  position: fixed;
}

.cursor-dot {
  z-index: 10000;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background: var(--black);
  pointer-events: none;
  overflow: hidden;
  transform: translate3d(0,0,0);
  position: fixed;
}

body {
  cursor: none; 
}
a {
  cursor: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

:root {
  --black: #322a2b;
  --message-back: #ffffff87;
  --white: #FFFFF0;
}

body {
  color: #322a2b;
  color: var(--black);
  background: rgb(174,182,202);
  background: linear-gradient(145deg, rgba(174,182,202,1) 0%, rgba(240,238,239,1) 65%, rgba(231,231,231,1) 100%);
}

a[class] {
  text-decoration: none;
}

@media screen and (min-width: 786px) {
  body{
    font-size: 20px;
  }
}
